<template>
  <div>
    <div id="top">
      <div id="top_btn" @click="show_tags">☰</div>
    </div>
    <transition name="tag" mode="out-in">
      <div id="tags" v-show="this.$store.state.show_tags">
        <div 
          class="tag"
          v-for="(tag, idx) in this.$store.state.resume" 
          :key="idx" 
          @click="change_tag(idx)"
          >
          {{ tag.name }}
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'Tags',
  methods: {
    change_tag(idx) {
      this.$store.state.current_tag = idx;
    },
    show_tags() {
      this.$store.state.show_tags = !this.$store.state.show_tags;
    }
  }
}
</script>

<style>
#top {
  position: sticky;
  z-index: 2;
  transform: translateZ(3px);
  background: #fff;
  box-shadow: 0 1px 5px #888;
}

#top_btn {
  height: 15vw;
  width: 15vw;
  /* font-size: 1.5em; */
  font-weight: 900;
  line-height: 15vw;
  text-align: center;
}

#tags {
  position: absolute;
  /* transform: translateZ(1px); */
  z-index: 1;
  width: 100%;
  background: #fff;
}

.tag {
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #888;
}

.tag-enter, .tag-leave-to {
  margin-top: -153px;
}
.tag-enter-active, .tag-leave-active {
  transition: .5s;
}
.tag-enter-to, .tag-leave {
  margin-top: 0;
}
</style>
