<template>
  <keep-alive>
    <div id="skills">
      <ul>
        <li 
          v-for="skill, idx in skills" 
          :key="idx"
          class="content"
          >
          <div class="name">
            <span>{{ skill.name }}</span>
            <span @click="show(idx)" class="show-btn">{{ skill.show ? '-' : '+' }}</span>
          </div>
          <transition 
            name="show"
            mode="out-in"
            >
            <div v-show="skill.show" class="detail">
              <p>{{ skill.detail }}</p>
            </div>
          </transition>
        </li>
      </ul>
    </div>
  </keep-alive>
</template>

<script>
export default {
  name: 'Skills',
  methods: { 
    show(idx) {
      this.skills[idx].show = !this.skills[idx].show;
    }
  },
  computed: {
    skills() {
      return this.$store.state.resume[1].content;
    }
  }
}
</script>

<style>
#skills {
  width: 100%;
}

.content {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10vw 0;
}

.name {
  background: rgb(179, 19, 27);
  position: sticky;
  width: 85%;
  color: #fff;
  height: 50px;
  line-height: 50px;
  /* font-size: 1.5em; */
  transform: translateZ(1px);
}

.detail {
  background: #fff;
  width: 85%;
  height: 150px;
  overflow-y: scroll;
}

.show-btn {
  float: right;
  margin-right: 15px;
  font-weight: bold;
}

.show-enter, .show-leave-to {
  margin-top: -150px;
}
.show-enter-active, .show-leave-active {
  transition: .5s;
}
.show-enter-to, .show-leave {
  margin-top: 0;
}
</style>