<template>
  <div id="home">
    <Tags/>
    <div id="home-content">
      <transition :name="direction">
        <component :is="target"/>
      </transition>
    </div>
  </div>
</template>

<script>
import Tags from '@/components/Tags.vue'
import Informations from '@/components/Informations.vue'
import Skills from '@/components/Skills.vue'
import Works from '@/components/Works.vue'

export default {
  name: 'Home',
  data() {
    return {
      sub_components: [Informations, Skills, Works],
      direction: ''
    }
  },
  computed: {
    target() {
      return this.sub_components[this.$store.state.current_tag];
    }
  },
  components: {
    Tags,
    Skills,
    Informations,
    Works
  },
  watch: {
    target(new_val, old_val) {
      this.direction = this.sub_components.indexOf(new_val) > this.sub_components.indexOf(old_val) ? 'left' : 'right';
    }
  }
}
</script>

<style>
#home {
  height: calc(100vh - 10vw);
  width: 100vw;
  height: 100vh;
  background: #fff;
  font-size: 2em;
  overflow: hidden;
}

#home-content {
  width: 100vw;
  position: relative;
  height: calc(100vh - 15vw);
}

.left-enter-active, .left-leave-active, .right-enter-active, .right-leave-active {
  transition: .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.left-enter-to,
.right-enter-to {
  transform: translate(0, 0);
}

.left-enter,
.right-leave-to {
  transform: translate(100%, 0);
}

.left-leave-to,
.right-enter {
  transform: translate(-100%, 0);
}
</style>