<template>
  <keep-alive>
    <div id="informations">
      <ul>
        <li 
          v-for="information, idx in informations" 
          :key="idx"
          class="content"
          >
          <div class="name">
            <span>{{ information.name }}</span>
            <span @click="show(idx)" class="show-btn">{{ information.show ? '-' : '+' }}</span>
          </div>
          <transition 
            name="show"
            mode="out-in"
            >
            <div v-show="information.show" class="detail">
              <p>{{ information.detail }}</p>
            </div>
          </transition>
        </li>
      </ul>
    </div>
  </keep-alive>
</template>

<script>
export default {
  name: 'Informations',
  methods: { 
    show(idx) {
      this.informations[idx].show = !this.informations[idx].show;
    }
  },
  computed: {
    informations() {
      return this.$store.state.resume[0].content;
    }
  }
}
</script>

<style>
#informations {
  width: 100%;
}
</style>
